import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { Observable } from 'rxjs';
import { AngularFirestore } from '@angular/fire/firestore';

import { Http ,HttpModule} from '@angular/http'

@Injectable({
  providedIn: 'root'
})
export class DataService {

  isAuth: boolean;
  flag: boolean = false; //for logout
  user: Observable<firebase.User>;
  name: string;
  list: Object;

  constructor(private firebaseAuth: AngularFireAuth, private db:AngularFirestore) { 
    this.user = firebaseAuth.authState;
    this.db.collection('/records').snapshotChanges().subscribe(data =>{
      this.list = data;
    })
    this.user.subscribe(result =>{
      if (result){
        this.isAuth = true
      }
      else{
        this.isAuth = false
      }
    })
  }

  add(obj){
    this.db.collection('/records').add(obj) 
  }

  signup(email: string, password: string) {
    this.firebaseAuth.auth.createUserWithEmailAndPassword(email, password).
    then(value => {
      console.log('Success!', value);
      this.name = value.user.email
      }).
      catch(err => {
        console.log('Something went wrong:',err.message);
      });    
  }

  login(email: string, password: string) {
    this.firebaseAuth.auth.signInWithEmailAndPassword(email, password).then(value => {
      console.log('Nice, it worked!', value);
      this.isAuth = true
      this.name = value.user.email
      this.flag = true
      }).catch(err => {
        this.flag = false
        this.isAuth = false
        alert("اسم المستخدم او كلمة المرور غير صحيحة")
        console.log('Something went wrong:',err.message);
      });
  }

  logout() {
    this.firebaseAuth.auth.signOut();
    //this.router.navigate(['/logReg']);
    this.flag = false
  }

  del(id: string){
    this.db.collection('/records').doc(id).delete().then(function() {
      console.log("Document successfully deleted!");
  }).catch(function(error) {
      console.error("Error removing document: ", error);
  });
  }
}
