import { Component } from '@angular/core';
import { DataService } from './data.service';
import { Router } from '@angular/router';

import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Alobaid Family';

  flag : boolean;

  ngOnChanges(){
    this.flag = this.data.flag
  }

  constructor(private data:DataService, private router:Router, private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) { 
      this.matIconRegistry.addSvgIcon(
        'exit',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/exit.svg')
    );
    this.matIconRegistry.addSvgIcon(
      'twitter',
      this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/twitter-brands.svg')
  );
    this.matIconRegistry.addSvgIcon(
      'instagram',
      this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/instagram-brands.svg')
  );
    
  }

  logout(){
    this.data.logout()
    this.router.navigate(['']);
  }

  goToLogin(){
    this.router.navigate(['/login']);
  }


}
