import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { AngularFirestore } from '@angular/fire/firestore';

import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

import swal from 'sweetalert';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  records: Object[] = [];

  constructor(private data:DataService, private db:AngularFirestore, private router:Router, private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) { 
      this.matIconRegistry.addSvgIcon(
        'edit',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/edit.svg')
    );
    
  }

  ngOnInit() {
    this.db.collection('records').snapshotChanges().subscribe(data =>{
      this.records = []
      for (var i in data){
        var r = data[i].payload.doc.data()
        r["id"] = data[i].payload.doc.id
        this.records.push(r)
      }
    })
  }

  logout(){
    this.data.logout()
    this.router.navigate(['/'])
  }

  delete(name,id){
    swal("هل تريد حذف القيد الخاص بـ "+ name, {
      buttons: ["لا", "نعم"],
    }).then((value) =>{
      if(value){
        this.data.del(id)
        // this.records = []
        // this.ngOnInit()
      }
    });
  }

}
